import noData from '@/components/noData'
import back from '@/components/back'

export default {
    components:{
        noData,
        back
    },
    data(){
        return {
            page: 1,
            haveMore: true,
            list: [],
            user: this.$store.state.userInfo.Data,
            scrollTopDistance: 0,
            pageShow: false,
            sorList: ['全部问诊', '全部订单'],
            index: null,
			allInquiryList: [
				
			],
            allOrderList: [
                {
                    name: '全部订单',
                    val: '-1'
                },
                {
                    name: '待问诊',
                    val: '0'
                },
                {
                    name: '问诊中',
                    val: '1'
				},
				{
					name: '已完成',
					val: '5'
				},
				{
					name: '已取消',
					val: '6'
				}
            ],
            allInquiry: {
                name: '全部问诊',
                val: '-1'
            },
            allOrder: {
                name: '全部订单',
                val: '-1'
            }
        }
    },
    created(){
		let dArr = [{
					name: '全部问诊',
					val: '-1'
				},
				{
					name: '图文问诊',
					val: '1'
				},
				{
					name: '视频问诊',
					val: '2'
				},
				{
					name: '名医续方',
					val: '3'
				},
				
				{
					name: '第二诊疗',
					val: '11'
				},
				{
					name: '远程门诊',
					val: '15'
				},
				{
					name: '面诊预约',
					val: '19'
				},
				{
					name: '多学科会诊咨询',
					val: '50'
				},
				{
					name: '专家会诊',
					val: '51'
				},
				{
					name: 'MDT会诊',
					val: '52'
				}]
		let nArr = [
			{
				name: '全部问诊',
				val: '-1'
			},
			{
				name: '护理咨询',
				val: '5'
			},
			{
				name: '远程护理',
				val: '6'
			},
		]
		if(this.user.User.DrType == 1) {
			this.allInquiryList = dArr
		}
		this.allInquiryList = this.user.User.DrType == 1 ? dArr : nArr
        this.pull(false)
    },
    mounted(){
        this.getMore()
        
    },
    beforeRouteLeave(to, from, next) {
        window.onscroll = null
        if(from.meta.keepAlive) {　
            from.meta.scollTopPosition = this.scrollTopDistance;
        }
        next()
    },
    beforeRouteEnter(to, from, next) { //进入前    	
        next(vm => {
            vm.getMore()
            setTimeout(() => {
                console.log(to.meta.scollTopPosition)
                vm.$refs.el.scrollTop = to.meta.scollTopPosition;
            }, 1)
        });
    },
    beforeDestroy(){
        window.onscroll = null
    },
    methods:{
		goDetail (item) {
			if( item.ServiceType == 50 || item.ServiceType == 51   ) { // 多学科会诊咨询 专家会诊 MDT会诊

				this.$router.push(`/order/multidisciplinary?id=${item.RelationId}`)

			}else if ( item.ServiceType == 52 ) {

                this.$router.push(`/order/multidisciplinary?id=${item.RelationId}&mdt=true`)

            }else if(item.ServiceType == 19) { // 面诊订单详情

				this.$router.push(`/order/faceCare?id=${item.RelationId}`)

			}else if(item.ServiceType == 11) { // 第二诊疗

				this.$router.push(`/order/secondDiagnosis?id=${item.RelationId}`)

			}else if(item.ServiceType == 1 || item.ServiceType == 2 || item.ServiceType == 3 || item.ServiceType == 15 || item.ServiceType == 5 || item.ServiceType == 6) { // 图文问诊

				this.$router.push(`/order/OrderDetail?id=${item.RelationId}&type=${item.ServiceType}`)
				
			}
		},
        chose(i) {
            this.allInquiry = i
            this.sorList[this.index] = i.name
            this.index = null
            this.haveMore = true
            this.page = 1
            this.pull(false)
        },
        chose1(i) {
            this.allOrder = i
            this.sorList[this.index] = i.name
            this.index = null
            this.haveMore = true
            this.page = 1
            this.pull(false)
        },
        sort(idx) {
            if(this.index == idx) {
                this.index = null
            } else {
                this.index = idx
            }
        },
        pull(h){
            if(this.haveMore){
                this.$post('/PlatFormAPI/PayOrder/QueryDoctorOrderMergePage',{
                    DrId: this.user.User.DoctorId,
                    ServiceType: this.allInquiry.val,
                    ServiceStatus: this.allOrder.val,
                    PageIndex: this.page,
                    PageSize: 15
                }).then(o => {
                    this.pageShow = true
                    if (!o.ReturnData || o.ReturnData.length < 15){
                        this.haveMore = false
                    }
                    else {
                        this.haveMore = true
                    }
                    
                    if (h){
                        o.ReturnData.forEach(v => {
                            this.list.push(v)
                        })
                    }
                    else {
                        if(o.ReturnData == null) {
                            o.ReturnData = []
                        }
                        this.list = o.ReturnData || []
                    }
                })
            }
        },
        getMore(){
            const el = this.$refs.el
            let timer = null
            el.onscroll = () => {
                clearTimeout(timer)
                timer = setTimeout(() => {
                    const top = el.scrollTop ||
							el.pageYOffset ||
							el.scrollTop,
							h = el.scrollHeight,
                            wh = window.innerHeight
						this.scrollTopDistance = top;
						if(h - top - wh < 100 && this.haveMore) {
							this.page += 1
							this.loading = true
							this.pull(true)
						}
                }, 100)
            }
        },
    }
}



